*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-family: sans-serif;
  font-size: 62.5%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff; }

::selection {
  text-shadow: none;
  background: #fa541c; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.8rem; }

h1, h2 {
  font-weight: 700; }

h3, h4, h5, h6 {
  font-weight: 400; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 2rem; }

h3 {
  font-size: 1.8rem; }

h4 {
  font-size: 1.6rem; }

h5, h6 {
  font-size: 1.4rem; }

@media (max-width: 575.98px) {
  h1 {
    font-size: 2.2rem; }
  h2 {
    font-size: 1.8rem; }
  h3 {
    font-size: 1.7rem; } }

p {
  margin-top: 0;
  margin-bottom: 1.6rem; }

a {
  color: #fa541c;
  text-decoration: none;
  background-color: transparent;
  transition: color .3s;
  -webkit-text-decoration-skip: objects; }
  a:hover {
    color: #fc8c67;
    text-decoration: none; }

/*!
 * Hamburgers
